import React, { Component } from "react";
import Header from "../../Header";
import styles from "./index.module.sass";
import Axios from "axios";

class Specs extends Component{
    constructor(props) {
        super(props);

        this.state = {
            specsList: [],
        };
    }

    componentDidMount() {
        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getSpecsA',
            params: {
                id: this.props.match.params.goodsId,
            },
        }).then((response) => {
            this.setState({
               specsList: response.data.data,
            });
        }).catch((error) => {
           console.log(error);
        });
    }

    goBack = () => {
        this.props.history.goBack();
    }

    render() {
        const { specsList } = this.state;

        return (
            <div>
                <div className={styles.pageBox}>
                    <Header/>
                    <div className={styles.detail}>
                        <div className={styles.backBtn}>
                            <i onClick={this.goBack}></i>
                        </div>
                        <div className={styles.name}>
                            <span>商品规格</span>
                        </div>
                        <div className={styles.content}>
                            <ul className={styles.list}>
                                {
                                    specsList && specsList.map((item,index) => {
                                        return (
                                            <li key={index} className={styles.item}>{item}</li>
                                        );
                                    })
                                }
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Specs;
