import React, { Component } from "react";
import Header from "../../Header";
import Recommend from "../Recommend";
import Axios from "axios";
import { Link } from "react-router-dom";
import styles from "./index.module.sass";

class Detail extends Component{
    constructor(props) {
        super(props);

        this.state = {
            recommendList: [],
            storyId: this.props.match.params.storyId ? this.props.match.params.storyId : '',
            data: [],
        };
    }

    componentDidMount() {
        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getserbFiA',
            params: {
                id: this.state.storyId,
            }
        }).then((response) => {
           this.setState({
               data: response.data.data.list,
               recommendList: response.data.data.pReco,
           });
        }).catch((error) => {
            console.log(error);
        });
    }

    goBack = () => {
        this.props.history.goBack();
    }

    render() {
        const { data, recommendList } = this.state;

        return (
            <div>
                <div className={styles.pageBox}>
                    <Header/>
                    <div className={styles.detail}>
                        <div className={styles.backBtn}>
                            <i onClick={this.goBack}></i>
                        </div>
                        <div>
                            <div className={styles.title}>{data.title}</div>
                            <div className={styles.time}>{data.time}</div>
                            <div className={styles.content}>
                                {
                                    data.content && data.content.map((item,index) => {
                                        return (
                                            <span key={index} className={item.type == 'pic' ? styles.pic : styles.text}>
                                                {item.type == 'pic' && <img src={item.content} />}
                                                {item.type == 'text' && <p>{item.content}</p>}
                                            </span>
                                        );
                                    })
                                }
                            </div>
                        </div>
                    </div>
                    <Recommend recommendList={recommendList} />
                </div>
            </div>
        );
    }
}

export default Detail;
