import React, { Component } from "react";
import Header from "../Header";
import Footer from "../Footer";
import List from "./List";
import { Link } from "react-router-dom";
import styles from "./index.module.sass";
import Axios from "axios";

class Series extends Component{

    constructor(props) {
        super(props);

        this.state = {
            windowHeight: window.screen.availHeight,
            seriesList: [],
            seriesListEnd: false,
            loading: false,
            pagination: {
                count: 0,
                current: 1,
                pageSize: 6,
            }
        };
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScrollLoadData)
        this.getSeriesListData()
    }

    handleScrollLoadData = () => {
        let docuementHeight = document.body.scrollHeight;
        let scrollTop = document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop;

        if((Number(scrollTop) + Number(this.state.windowHeight)) > (Number(docuementHeight) - 200)){
            this.getSeriesListData()
        }
    }

    getSeriesListData = () => {
        if(this.state.seriesListEnd){
            return false;
        }

        if(this.state.loading){
            return false;
        }

        this.setState({
            loading: true,
        });

        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getsDatliA',
            params: {
                pageSize: this.state.pagination.pageSize,
                current: this.state.pagination.current
            }
        }).then((response) => {
            /* 第一页，并且没有搜索结果就请求模块数据 */
            if(response.data.data.pagination.count === 0 && response.data.data.pagination.current <= 1){
                this.setState({
                    seriesList:[],
                    seriesListEnd: true,
                    loading: false,
                });

                return false;
            }

            /* 翻页到最后一页加一页 */
            if(response.data.data.list.length === 0 && response.data.data.pagination.current > 1){
                this.setState({
                    seriesListEnd: true,
                    loading: false,
                });

                return false;
            }

            this.setState({
                pagination: {
                    count: response.data.data.pagination.count,
                    current: Number(response.data.data.pagination.current) + 1,
                    pageSize: response.data.data.pagination.pageSize
                },
                seriesList: this.state.seriesList.concat(response.data.data.list),
                loading: false,
            })
        }).catch((error) => {
            console.log(error);
        });
    }

    goBack = () => {
        this.props.history.goBack();
    }

    render() {
        const { seriesList, seriesListEnd } = this.state;

        return (
            <div>
                <div className={styles.pageBox}>
                    <Header/>
                    <div className={styles.box}>
                        <div className={styles.name}>
                            <i onClick={this.goBack}></i>
                            <span>品牌系列</span>
                        </div>
                        <List seriesList={seriesList} />
                        { seriesListEnd && <div className={styles.listEnd}>没有更多了</div>}
                    </div>
                    <Footer pageName="series" />
                </div>
            </div>
        );
    }
}

export default Series;
