import React, { Component } from "react";
import Swiper from "swiper";
import ReactSwiper from "reactjs-swiper";
import { Link } from "react-router-dom";
import styles from "./index.module.sass";
import IndexStyles from "./index.sass";
import Axios from "axios";

class Series extends Component{
    constructor(props) {
        super(props);

        this.state = {
            seriesList: [],
        }
    }

    componentDidMount() {
        this.getSeriesData()
    }

    getSeriesData = () => {
        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getSeriA'
        }).then((response) => {
            this.setState({
                seriesList: response.data.data
            },function () {
                new Swiper ('.series-swiper', {
                    loop: true,
                    autoplay: 5000,
                    slidesPerView: "auto",
                    centeredSlides:true,
                    spaceBetween: 20,
                })
            })
        }).catch((error) => {
            console.log(error);
        });
    }

    render() {
        const { seriesList } = this.state;

        return (
            <div>
                <div className={styles.box}>
                    <div className={styles.name}>
                        <span>品牌系列</span>
                    </div>
                    <div className={styles.list}>
                        <div className="swiper-container series-swiper">
                            <div className="swiper-wrapper">
                                {
                                    seriesList && seriesList.map((item) =>
                                        <div key={item.id} className="swiper-slide">
                                            <div>
                                                <img src={item.image} />
                                            </div>
                                            <Link to={"/seriesDetail/"+item.id}>
                                                <span>{item.title}</span>
                                                <i></i>
                                            </Link>
                                        </div>
                                    )
                                }
                            </div>
                        </div>
                        <div className={styles.moreLink}>
                            <Link to="/series">查看更多系列</Link>
                        </div>
                    </div>

                </div>
            </div>
        );
    }
}

export default Series;
