import React,{ Component } from 'react';
import { NavLink } from "react-router-dom";
import styles from "./index.module.sass";

class Footer extends Component{

    constructor(props) {
        super(props);

        this.state = {
            defaultPageName: 'story',
        }
    }


    componentDidMount() {
    }

    render() {
        const pageName = this.props.pageName ? this.props.pageName : this.state.defaultPageName;


        return (
            <div>
                <div className={styles.box}>
                    <div className={styles.staticBox}></div>
                    <div className={styles.footerBox}>
                        <ul className={styles.footerList}>
                            <li className={styles.footerItem}>
                                <div>
                                    <NavLink to="/" exact strict activeClassName={styles.selectStatus}>首页</NavLink>
                                </div>
                            </li>
                            <li className={styles.footerItem}>
                                <div>
                                    <NavLink to="/classify" activeClassName={styles.selectStatus}>分类</NavLink>
                                </div>
                            </li>
                            <li className={styles.footerItem}>
                                <div>
                                    <NavLink to={"/"+pageName} exact strict activeClassName={styles.selectStatus}>故事</NavLink>
                                </div>
                            </li>
                            <li className={styles.footerItem}>
                                <div>
                                    <NavLink to="/about" exact strict activeClassName={styles.selectStatus}>关于</NavLink>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}

export default Footer;
