import React, { Component } from "react";
import Header from "../../Header";
import Swiper from "swiper";
import ReactSwiper from "reactjs-swiper";
import Axios from "axios";
import { Link } from "react-router-dom";
import styles from "./index.module.sass";
import IndexStyles from "./index.sass";
import Recommend from "../Recommend";

class Detail extends Component{
    constructor(props){
        super(props);

        this.state = {
            data: [],
            recommendList: [],
            goodsId: this.props.match.params.goodsId ? this.props.match.params.goodsId : '',
        };
    }

    componentDidMount() {
        Axios({
            methods: 'get',
            'url': 'http://123.57.11.135/getproFiA',
            params: {
                id: this.state.goodsId,
            },
        }).then((response) => {
            console.log(response);
            this.setState({
                data: response.data.data.list,
                recommendList: response.data.data.pReco,
            },() => {
                /*如果只有一个slide就销毁swiper*/
                if(this.state.data.image.length <= 1){
                    let goodsDetailSwiper = new Swiper('.goods-detail-swiper',{
                        loop: true, // 循环模式选项
                        autoplay:{
                            delay:2000,
                            disableOnInteraction:false, //解决滑动后不能轮播的问题
                        },
                    });
                    goodsDetailSwiper.destroy();
                }else{
                    let goodsDetailSwiper = new Swiper('.goods-detail-swiper',{
                        loop: true, // 循环模式选项
                        autoplay:{
                            delay:2000,
                            disableOnInteraction:false, //解决滑动后不能轮播的问题
                        },
                        pagination: {
                            el: '.swiper-pagination',
                        },
                    });
                }
            });
            console.log(this.state);
        }).catch((error) => {
            console.log(error);
        });
    }

    goBack = () => {
        this.props.history.goBack();
    }

    render() {
        const { data, recommendList, goodsId } = this.state;

        return (
            <div>
                <div className={styles.pageBox}>
                    <Header/>
                    <div className={styles.goodsDetail}>
                        <div className={styles.name}>
                            <i onClick={this.goBack}></i>
                            <span>商品详情</span>
                        </div>
                        <div className={styles.goodsDetailBox}>
                            <div className="swiper-container goods-detail-swiper">
                                <div className="swiper-wrapper">
                                    {
                                        data.image && data.image.map((item,index) =>
                                            <div key={index} className="swiper-slide">
                                                <div>
                                                    <img src={item} />
                                                </div>
                                            </div>
                                        )
                                    }
                                </div>
                                <div className="swiper-pagination"></div>
                            </div>
                            <div className={styles.title}>{data.title}</div>
                            <div className={styles.price}>￥{data.price}</div>
                            <div className={styles.goodsSummary}>
                                <span>
                                    <Link to={"/goodsExplain/"+goodsId}>服务说明</Link>
                                </span>
                                <span>
                                    <Link to={"/goodsSpecs/"+goodsId}>商品规格</Link>
                                </span>
                            </div>
                            <div className={styles.detailModuleName}>商品描述</div>
                            <div className={styles.detail}>
                                {
                                    data.content && data.content.map((item,index) => {
                                        return (
                                            <span key={index} className={item.type == 'pic' ? styles.pic : styles.text}>
                                                {item.type == 'pic' && <img src={item.content} />}
                                                {item.type == 'text' && <p>{item.content}</p>}
                                            </span>
                                        );
                                    })
                                }
                            </div>
                        </div>
                        <Recommend recommendList={recommendList} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Detail;
