import React, { Component } from 'react';
import Swiper from "swiper";
import ReactSwiper from "reactjs-swiper";
import styles from "./index.module.sass";
import IndexStyles from "./index.sass";
import {Link} from "react-router-dom";
import Axios from "axios";

class Banner extends Component{

    constructor(props){
        super(props);

        this.state = {
            bannerList: [],
        }
    }

    componentDidMount() {
        this.getBannerData()
    }

    getBannerData = () => {
        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getBraA',
        }).then((response) => {
            this.setState({
                bannerList: response.data.data
            },function () {
                /*如果只有一个slide就销毁swiper*/
                if(this.state.bannerList.length <= 1){
                    let bannerSwiper = new Swiper('.banner-swiper',{
                        loop: true, // 循环模式选项
                        autoplay:{
                            delay:2000,
                            disableOnInteraction:false, //解决滑动后不能轮播的问题
                        },
                    });
                    bannerSwiper.destroy();
                }else{
                    let bannerSwiper = new Swiper('.banner-swiper',{
                        loop: true, // 循环模式选项
                        autoplay:{
                            delay:2000,
                            disableOnInteraction:false, //解决滑动后不能轮播的问题
                        },
                        pagination: {
                            el: '.swiper-pagination',
                        },
                    });
                }
            });
        }).catch((error) => {
            console.log(error);
        })
    }

    render() {
        const { bannerList } = this.state;

        return (
            <div>
                <div className={styles.box}>
                    <div className="swiper-container banner-swiper">
                        <div className="swiper-wrapper">
                            {
                                bannerList.map((item) =>
                                    <div key={item.id} className="swiper-slide">
                                        <Link to={"/storyDetail/"+item.id}>
                                            <img src={item.image} />
                                        </Link>
                                    </div>
                                )
                            }
                        </div>
                        <div className="swiper-pagination"></div>
                    </div>

                </div>
            </div>
        );
    }
}

export default Banner;
