import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter as Router, Route, Switch, Redirect } from "react-router-dom";
import styles from "./index.sass";
import Home from "./views/Home";
import Classify from "./views/Classify";
import Series from "./views/Series";
import About from "./views/About";
import Story from "./views/Story";
import Search from "./views/Search";
import SeriesDetail from "./views/Series/Detail";
import StoryDetail from "./views/Story/Detail";
import GoodsDetail from "./views/Goods/Detail";
import ScrollToTop from "./ScrollToTop";
import AboutDetail from "./views/About/Detail";
import AboutContact from "./views/About/Contact";
import GoodsSpecs from "./views/Goods/Specs";
import GoodsExplain from "./views/Goods/Explain";
import AboutNotice from "./views/About/Notice";

const Page = () => {
    return (
        <div className={styles.rootDiv}>
            <Router>
                <ScrollToTop>
                    <Switch>
                        <Route exact strict path="/" component={Home} />
                        <Route exact strict path="/search" component={Search} />
                        <Route exact strict path="/search/:keyWord" component={Search} />
                        <Route exact strict path="/classify" component={Classify} />
                        <Route exact strict path="/classify/:classId" component={Classify} />
                        <Route exact strict path="/series" component={Series} />
                        <Route exact strict path="/seriesDetail/:seriesId" component={SeriesDetail} />
                        <Route exact strict path="/story" component={Story} />
                        <Route exact strict path="/storyDetail/:storyId" component={StoryDetail} />
                        <Route exact strict path="/goodsDetail/:goodsId" component={GoodsDetail} />
                        <Route exact strict path="/goodsSpecs/:goodsId" component={GoodsSpecs} />
                        <Route exact strict path="/goodsExplain/:goodsId" component={GoodsExplain} />
                        <Route exact strict path="/about" component={About} />
                        <Route exact strict path="/aboutDetail" component={AboutDetail} />
                        <Route exact strict path="/aboutNotice" component={AboutNotice} />
                        <Route exact strict path="/aboutContact" component={AboutContact} />
                        <Redirect path="*" to="/404"/>
                    </Switch>
                </ScrollToTop>
            </Router>
        </div>
    );
}

ReactDOM.render(<Page/>, document.getElementById('root')
);
