import React, { Component } from 'react';
import Header from "../Header";
import Footer from "../Footer";
import List from "./List";
import Axios from "axios";
import styles from "./index.module.sass";
import {NavLink} from "react-router-dom";

class Classify extends Component{

    constructor(props) {
        super(props);

        this.state = {
            windowHeight: window.screen.availHeight,
            loading: false,
            goodsList: [],
            goodsListEnd: false,
            classId: this.props.match.params.classId ? this.props.match.params.classId : '',
            allNodesStatus: false,
            pagination: {
                count: 0,
                current: 1,
                pageSize: 8,
            },
            classList: []
        };
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScrollLoadData);

        /* 如果url不携带id，默认跳转id为第一级 */
        if(!this.state.classId){
            this.props.history.push('/classify/1');
            return false;
        }

        this.getClassListData()
        this.getGoodsListData()
    }

    componentDidUpdate(prevProps) {
        /* 如果上个url的classId和当前页面的classId不一致就更新数据 */
        if(prevProps.match.params.classId !== this.props.match.params.classId){
            this.setState({
                classId: this.props.match.params.classId ? this.props.match.params.classId : prevProps.match.params.classId,
                goodsList: [],
                goodsListEnd: false,
                pagination: {
                    count: 0,
                    current: 1,
                    pageSize: 8,
                },
            },() => {
                if(this.state.classList.length < 1){
                    this.getClassListData()
                }
                this.getGoodsListData()
            })
        }
    }

    handleScrollLoadData = () => {
        let docuementHeight = document.body.scrollHeight;
        let scrollTop = document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop;

        if((Number(scrollTop) + Number(this.state.windowHeight)) > (Number(docuementHeight) - 200)){
            this.getGoodsListData();
        }
    }

    getGoodsListData = () => {
        if(this.state.goodsListEnd){
            return false;
        }

        if(this.state.loading){
            return false;
        }

        this.setState({
            loading: true,
        });

        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getproLiA?id=1',
            params: {
                pageSize: this.state.pagination.pageSize,
                current: this.state.pagination.current,
                id: this.state.classId,
            }
        }).then((response) => {
            /* 第一页，并且没有搜索结果 */
            if(response.data.data.pagination.count === 0 && response.data.data.pagination.current <= 1){
                this.setState({
                    goodsList:[],
                    goodsListEnd: true,
                    loading: false,
                });

                return false;
            }

            /* 翻页到最后一页加一页 */
            if(response.data.data.list.length === 0 && response.data.data.pagination.current > 1){
                this.setState({
                    goodsListEnd: true,
                    loading: false,
                });
                return false;
            }

            this.setState({
                pagination: {
                    count: response.data.data.pagination.count,
                    current: Number(response.data.data.pagination.current) + 1,
                    pageSize: response.data.data.pagination.pageSize
                },
                goodsList: this.state.goodsList.concat(response.data.data.list),
                loading: false,
            })
        }).catch((error) => {
            console.log(error);
        });
    }

    getClassListData = () => {
        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getserLiA',
            params: {
                id: this.state.classId ? this.state.classId : '',
            }
        }).then((response) => {
            this.setState({
                classList: response.data.data,
            },() => {
                console.log(this.state.classList);
            })
        }).catch((error) => {
            console.log(error);
        });
    }

    hideAllNodesBox = (data) => {
        if(data != 'no' && data != 'yes'){
            let trigger = data.currentTarget.getAttribute('data-trigger');
            let name = data.currentTarget.getAttribute('data-name');

            if(name == '全部'){
                this.setState({
                    classId: '',
                },() => {
                    this.getClassListData()
                })
            }

            data = trigger;
        }


        if(data == 'no'){
            return false;
        }

        this.setState({
            allNodesStatus: false,
        })
    }

    showAllNodesBox = (event) => {
        console.log(event.target.classId);
        this.getClassListData()

        this.setState({
            allNodesStatus: true,
        })
    }

    handleSelectClassify = (event) => {
        let classifyId = event.currentTarget.getAttribute('class-id');
        this.setState({
           classId:  classifyId,
        },() => {
            this.hideAllNodesBox('yes');
            this.getClassListData();
            this.props.history.push('/classify/'+classifyId);
        });
        return false;
    }

    render() {
        const { classId, goodsList, classList, goodsListEnd, allNodesStatus } = this.state;

        return (
            <div>
                <div className={styles.pageBox}>
                    <Header/>
                    <div className={styles.box}>
                        <div className={styles.classBox}>
                            <ul className={styles.list}>
                                <li className={styles.classItem}>
                                    {
                                        classList.Nodes &&
                                        <span onClick={this.showAllNodesBox}>
                                            <b>{classList.Nodes.name}</b>
                                            <i></i>
                                        </span>
                                    }
                                </li>
                                {
                                    classList.Nodes && classList.Nodes.childNodes.map((item,index) => {
                                        return (
                                            <li key={index} className={styles.classItem}>
                                                {
                                                    item.childNodes.length > 0 ?
                                                        <span onClick={this.showAllNodesBox}>
                                                            <b>{item.name}</b>
                                                             <i></i>
                                                        </span>
                                                    :
                                                        <NavLink exact strict to={'/classify/'+item.id} activeClassName={styles.selectClassStatus}>
                                                            <b>{item.name}</b>
                                                        </NavLink>
                                                }
                                            </li>
                                        );
                                    })
                                }
                            </ul>
                            <span className={styles.lucencyBlock}></span>
                        </div>
                    </div>
                    <div data-trigger="yes" className={allNodesStatus ? styles.classifyListPage : styles.hideClassifyListPage}>
                        <div className={styles.background} data-trigger="yes" data-name="page" onClick={this.hideAllNodesBox.bind('yes')}></div>
                        <div className={styles.classifyListBox}>
                            <div className={styles.classifyListName} data-trigger="no" data-name="商品分类" onClick={this.hideAllNodesBox.bind('no')}>商品分类</div>
                            <div className={styles.classifyList}>
                                {
                                    classList.allNodes && classList.allNodes.map((item,index) => {
                                        return (
                                            <section key={index} className={styles.classifyListSection}>
                                                <span data-trigger={item.name == "全部" ? 'yes' : 'no'} data-name={item.name} onClick={this.hideAllNodesBox.bind('no')}>{item.name}</span>
                                                <ul className={styles.classifyListUl}>
                                                    {
                                                        item.childNodes && item.childNodes.map((value,idx) => {
                                                            return (
                                                                <li key={idx} className={styles.classifyListItem}>
                                                                    <span data-trigger="yes" data-name={value.name} class-id={value.id} onClick={this.handleSelectClassify.bind(this)} className={classId == value.id ? styles.selectClassifyStatus : ''}>
                                                                        {value.name}
                                                                    </span>
                                                                </li>
                                                            );
                                                        })
                                                    }
                                                </ul>
                                            </section>
                                        );
                                    })
                                }
                            </div>
                        </div>
                    </div>
                    <List goodsList={goodsList} />
                    { goodsListEnd && <div className={styles.listEnd}>没有更多了</div>}
                    <Footer/>
                </div>
            </div>
        );
    }
}

export default Classify;
