import React from "react";
import Header from "../Header";
import Footer from "../Footer";
import List from "./List";
import styles from "./index.module.sass";
import Axios from "axios";

class Story extends React.Component{

    constructor(props) {
        super(props);

        this.state = {
            windowHeight: window.screen.availHeight,
            storyList: [],
            storyListEnd: false,
            loading: false,
            pagination: {
                total: 0,
                current: 1,
                pageSize: 6,
            }
        };
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScrollLoadData)
        this.getStoryListData()
    }

    handleScrollLoadData = () => {
        let docuementHeight = document.body.scrollHeight;
        let scrollTop = document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop;


        if((Number(scrollTop) + Number(this.state.windowHeight)) > (Number(docuementHeight) - 200)){
            this.getStoryListData()
        }
    }

    getStoryListData = () => {
        if(this.state.storyListEnd){
            return false;
        }

        if(this.state.loading){
            return false;
        }

        this.setState({
            loading: true,
        });

        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getserbLiA',
            params: {
                pageSize: this.state.pagination.pageSize,
                current: this.state.pagination.current,
                id: 1,
            }
        }).then((response) => {
            /* 第一页，并且没有搜索结果就请求模块数据 */
            if(response.data.data.pagination.count === 0 && response.data.data.pagination.current <= 1){
                this.setState({
                    storyList:[],
                    storyListEnd: true,
                    loading: false,
                });

                return false;
            }

            /* 翻页到最后一页加一页 */
            if(response.data.data.list.length === 0 && response.data.data.pagination.current > 1){
                this.setState({
                    storyListEnd: true,
                    loading: false,
                });

                return false;
            }

            this.setState({
                pagination: {
                    total: response.data.data.pagination.count,
                    current: Number(response.data.data.pagination.current) + 1,
                    pageSize: response.data.data.pagination.pageSize
                },
                storyList: this.state.storyList.concat(response.data.data.list),
                loading: false,
            })
        }).catch((error) => {
            console.log(error)
        });
    }

    render() {
        const { storyList, storyListEnd } = this.state;

        return (
            <div>
                <div className={styles.pageBox}>
                    <Header/>
                    <div className={styles.box}>
                        <div className={styles.name}>
                            <span>品牌故事</span>
                        </div>
                        <List storyList={storyList} />
                        { storyListEnd && <div className={styles.listEnd}>没有更多了</div>}
                    </div>
                    <Footer pageName="story" />
                </div>
            </div>
        );
    }

}

export default Story;
