import React, { Component } from 'react';
import Header from "../Header";
import Banner from "./Banner";
import Tag from "./Tag";
import Recommend from "./Recommend";
import Story from "./Story";
import { Link } from "react-router-dom";
import styles from "./index.module.sass";
import Series from "./Series";
import Footer from "../Footer";
import Axios from "axios";

class Home extends Component{

    constructor(props) {
        super(props);

        this.state = {
            recommendList: [],
            storyList: [],
        };
    };

    componentDidMount() {
        this.getRecommendData()
        this.getStoryData()
    };

    getRecommendData = () => {
        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getProA',
        }).then((response) => {
            this.setState({
                recommendList: response.data.data.list,
            })
        }).catch((error) => {
            console.log(error);
        });
    };

    getStoryData = () => {
        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getBrStoA'
        }).then((response) => {
            this.setState({
                storyList: response.data.data.list
            })
        }).catch((error) => {
            console.log(error);
        });
    };

    render() {
        const { recommendList, storyList } = this.state;

        return (
            <div>
                <div className={styles.pageBox}>
                    <Header/>
                    <Banner/>
                    <Tag/>
                    <Recommend name="最热推荐" recommendList={recommendList} />
                    <div className={styles.hotGoodsBtn}>
                        <Link to="/classify/1">
                            <span>查看更多热门商品，请点击此处。</span>
                            <i></i>
                        </Link>
                    </div>
                    <Story name="品牌故事" storyList={storyList} />
                    <Series/>
                    <Footer/>
                </div>
            </div>
        );
    }
}

export default Home;
