import React, { Component } from "react";
import Recommend from "./Recommend";
import List from "./List";
import Axios from "axios";
import styles from "./index.module.sass";
import {Link} from "react-router-dom";

class Search extends Component{
    constructor(props) {
        super(props);

        this.state = {
            windowHeight: window.screen.availHeight,
            keyWord: this.props.match.params.keyWord ? this.props.match.params.keyWord : '',
            isModifyKeyWord: false,
            loading: false,
            goodsList: [],
            goodsListEnd: false,
            recommendList: [],
            isFirst: this.props.match.params.keyWord ? false : true, // 是否首次进入，并且不带参数。用于显示模块
            pagination: {
                count: 0,
                current: 1,
                pageSize: 8,
            }
        };
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScrollLoadGoods)

        this.getGoodsListData()
    }

    componentDidUpdate() {
        if(this.state.isModifyKeyWord) {
            return false;
        }

        //监听路由变化
        if(this.props.match.params.keyWord !== this.state.keyWord && this.props.match.params.keyWord !== undefined){
            this.setState({
                keyWord: this.props.match.params.keyWord ? this.props.match.params.keyWord : '',
            },function () {
                this.handleClickSearchBtn()
            })
        }
    }

    handleScrollLoadGoods = () => {
        if(this.state.keyWord === '' || this.state.keyWord === undefined || this.state.keyWord === null){
            return false;
        }

        let docuementHeight = document.body.scrollHeight;
        let scrollTop = document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop;

        if((Number(scrollTop) + Number(this.state.windowHeight)) > (Number(docuementHeight) - 200)){
            this.getGoodsListData()
        }
    }

    handleInputChange = (event) => {
        this.setState({
            keyWord: event.target.value,
            isModifyKeyWord: true,
        })
    }

    handleClickSearchBtn = () => {
        /* 如果新搜索词和上次搜索词一致，就不请求搜索 */
        if(this.props.match.params.keyWord === this.state.keyWord){
            return false;
        }

        this.props.history.push('/search/'+this.state.keyWord);

        this.setState({
            goodsList: [],
            goodsListEnd: false,
            isModifyKeyWord: false,
            pagination: {
                count: 0,
                current: 1,
                pageSize: 8,
            }
        }, () => {
            this.getGoodsListData()
        });
    }

    getGoodsListData = () => {
        /* 如果url上没有参数，就不请求商品列表，之请求模块数据 */
        if( !this.state.keyWord ){
            console.log(this.state.keyWord);
            this.getRecommendListData()
            return false;
        }

        if(this.state.goodsListEnd){
            return false;
        }

        if(this.state.loading){
            return false;
        }

        this.setState({
            loading: true,
        });

        this.getRecommendListData();

        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getproSeA',
            params: {
                pageSize: this.state.pagination.pageSize,
                current: this.state.pagination.current,
                name: this.state.keyWord,
            },
            responseType: 'json',
        }).then((response) => {
            this.setState({
                isFirst: false,
            });

            if(response.data.msg === '失败'){
                this.setState({
                    goodsList:[],
                    goodsListEnd: true,
                    loading: false,
                });
                return false;
            }

            /* 第一页，并且没有搜索结果就请求模块数据 */
            if(response.data.data.pagination.count === 0 && response.data.data.pagination.current <= 1){
                this.setState({
                    goodsList:[],
                    goodsListEnd: true,
                    loading: false,
                });

                return false;
            }

            /* 翻页到最后一页加一页 */
            if(response.data.data.list.length === 0 && response.data.data.pagination.current > 1){
                this.setState({
                    goodsListEnd: true,
                    loading: false,
                });

                return false;
            }

            this.setState({
                pagination: {
                    count: response.data.data.pagination.count,
                    current: Number(response.data.data.pagination.current) + 1,
                    pageSize: response.data.data.pagination.pageSize
                },
                goodsList: this.state.goodsList.concat(response.data.data.list),
                loading: false,
            });
        }).catch((error) => {
            console.log(error);
        });
    }

    getRecommendListData = () => {
        if( this.state.recommendList.length !== 0 ){
            return false;
        }

        Axios({
            methods: 'get',
            url: 'http://123.57.11.135/getpseHoA',
        }).then((response) => {
            this.setState({
                recommendList: response.data.data,
            })
        }).catch((error) => {
            console.log(error)
        });
    }

    goBack = () => {
        this.props.history.goBack();
    }

    render() {
        const { keyWord,isFirst,recommendList,goodsList,pagination,goodsListEnd  } = this.state;

        return (
            <div>
                <div className={styles.pageBox}>
                    <div className={styles.searchBox}>
                        <div>
                            <i onClick={this.goBack}></i>
                        </div>
                        <div>
                            <label className={ keyWord.length < 1 ? styles.noInput : styles.yesInput }></label>
                            <input type="text" value={keyWord} onChange={this.handleInputChange} placeholder="请输入关键词进行搜索"/>
                        </div>
                        <div>
                            <span onClick={this.handleClickSearchBtn}>搜索</span>
                        </div>
                    </div>
                    {
                        isFirst &&
                        <div>
                            <div className={styles.nullBox}></div>
                            <Recommend name="最热推荐" recommendList={recommendList} />
                        </div>
                    }
                    {
                        (!isFirst && pagination.count === 0) &&
                        <div>
                            <div className={styles.nullBox}>暂无搜索结果，换个关键词试试~</div>
                            <Recommend name="最热推荐" recommendList={recommendList} />
                        </div>
                    }
                    {
                        pagination.count > 0 &&
                        <List goodsList={goodsList} />
                    }
                    { (goodsListEnd && goodsList.length > 0) && <div className={styles.listEnd}>没有更多了</div>}
                </div>
            </div>
        );
    }
}

export default Search;
